import React, { useState, useEffect } from 'react';
import { format } from 'date-fns';
import { DailyPlan, PlannedWorkout } from '../types';
import { calculateCaloriesBurned } from '../utils/calculations';

export const DailyPlanView: React.FC = () => {
  const [dailyPlan, setDailyPlan] = useState<DailyPlan | null>(null);
  const [editingMinutes, setEditingMinutes] = useState<string | null>(null);
  const [minutes, setMinutes] = useState<string>('15');

  useEffect(() => {
    // Load today's plan from localStorage
    const today = format(new Date(), 'yyyy-MM-dd');
    const savedPlans = localStorage.getItem('dailyPlans');
    
    if (savedPlans) {
      const plans: DailyPlan[] = JSON.parse(savedPlans);
      const todaysPlan = plans.find(plan => plan.date === today);
      
      if (todaysPlan) {
        setDailyPlan(todaysPlan);
      } else {
        // Create new plan for today
        const newPlan: DailyPlan = {
          date: today,
          workouts: [],
        };
        setDailyPlan(newPlan);
        
        // Save to localStorage
        localStorage.setItem('dailyPlans', JSON.stringify([...plans, newPlan]));
      }
    } else {
      // No plans exist, create a new one
      const newPlan: DailyPlan = {
        date: today,
        workouts: [],
      };
      setDailyPlan(newPlan);
      
      // Save to localStorage
      localStorage.setItem('dailyPlans', JSON.stringify([newPlan]));
    }
  }, []);

  const toggleCompleted = (id: string) => {
    if (!dailyPlan) return;
    
    const updatedWorkouts = dailyPlan.workouts.map(workout => 
      workout.id === id ? { ...workout, completed: !workout.completed } : workout
    );
    
    updatePlan({ ...dailyPlan, workouts: updatedWorkouts });
  };

  const updateMinutes = (id: string) => {
    if (!dailyPlan || !minutes) return;
    
    const mins = parseInt(minutes);
    if (isNaN(mins) || mins <= 0) return;
    
    const updatedWorkouts = dailyPlan.workouts.map(workout => 
      workout.id === id ? { ...workout, minutes: mins } : workout
    );
    
    updatePlan({ ...dailyPlan, workouts: updatedWorkouts });
    setEditingMinutes(null);
  };

  const removeWorkout = (id: string) => {
    if (!dailyPlan) return;
    
    const updatedWorkouts = dailyPlan.workouts.filter(workout => workout.id !== id);
    updatePlan({ ...dailyPlan, workouts: updatedWorkouts });
  };

  const updatePlan = (plan: DailyPlan) => {
    setDailyPlan(plan);
    
    // Update in localStorage
    const savedPlans = localStorage.getItem('dailyPlans');
    
    if (savedPlans) {
      const plans: DailyPlan[] = JSON.parse(savedPlans);
      const updatedPlans = plans.map(p => p.date === plan.date ? plan : p);
      
      localStorage.setItem('dailyPlans', JSON.stringify(updatedPlans));
    } else {
      localStorage.setItem('dailyPlans', JSON.stringify([plan]));
    }
  };

  const calculateTotalCalories = (): number => {
    if (!dailyPlan || dailyPlan.workouts.length === 0) return 0;
    
    return dailyPlan.workouts.reduce((total, workout) => {
      return total + (workout.caloriesPerMinute * workout.minutes);
    }, 0);
  };

  const calculateCompletedCalories = (): number => {
    if (!dailyPlan || dailyPlan.workouts.length === 0) return 0;
    
    return dailyPlan.workouts
      .filter(workout => workout.completed)
      .reduce((total, workout) => {
        return total + (workout.caloriesPerMinute * workout.minutes);
      }, 0);
  };

  // Save calories to daily data
  const saveCaloriesToDailyData = () => {
    const completedCalories = calculateCompletedCalories();
    const today = format(new Date(), 'yyyy-MM-dd');
    
    // Load existing daily data
    const savedData = localStorage.getItem('exerciseHistory');
    
    if (savedData) {
      const historyData = JSON.parse(savedData);
      const todayEntry = historyData.find((entry: any) => entry.date === today);
      
      if (todayEntry) {
        // Update existing entry
        const updatedHistory = historyData.map((entry: any) => {
          if (entry.date === today) {
            // Add planned workout calories to any existing calories
            const totalBurned = completedCalories + (entry.plannedWorkoutCalories || 0);
            return { 
              ...entry, 
              plannedWorkoutCalories: completedCalories 
            };
          }
          return entry;
        });
        
        localStorage.setItem('exerciseHistory', JSON.stringify(updatedHistory));
      } else {
        // Create a new entry with just the planned workout calories
        const newEntry = {
          date: today,
          pushups: 0,
          crunches: 0,
          jumpingJacks: 0,
          milesWalked: 0,
          milesRan: 0,
          caloriesEaten: 0,
          proteinGrams: 0,
          plannedWorkoutCalories: completedCalories
        };
        
        localStorage.setItem('exerciseHistory', JSON.stringify([...historyData, newEntry]));
      }
    } else {
      // No history exists, create a new one
      const newEntry = {
        date: today,
        pushups: 0,
        crunches: 0,
        jumpingJacks: 0,
        milesWalked: 0,
        milesRan: 0,
        caloriesEaten: 0,
        proteinGrams: 0,
        plannedWorkoutCalories: completedCalories
      };
      
      localStorage.setItem('exerciseHistory', JSON.stringify([newEntry]));
    }
    
    alert(`${completedCalories} calories from completed workouts saved to your daily total!`);
  };

  if (!dailyPlan) {
    return <div className="text-center p-8">Loading today's plan...</div>;
  }

  return (
    <div className="max-w-4xl mx-auto p-4">
      <div className="flex justify-between items-center mb-6">
        <h2 className="text-2xl font-bold">Today's Workout Plan</h2>
        <div className="text-right">
          <p className="text-sm text-gray-600">{format(new Date(), 'MMMM d, yyyy')}</p>
          <p className="font-semibold">
            Total: {calculateTotalCalories()} calories 
            / Completed: {calculateCompletedCalories()} calories
          </p>
        </div>
      </div>
      
      {dailyPlan.workouts.length === 0 ? (
        <div className="bg-white p-8 rounded shadow text-center">
          <p className="text-lg mb-4">No workouts planned for today yet.</p>
          <p className="text-gray-600">
            Visit the <a href="/library" className="text-blue-500 hover:underline">workout library</a> to add exercises!
          </p>
        </div>
      ) : (
        <>
          <div className="space-y-4 mb-6">
            {dailyPlan.workouts.map(workout => (
              <div 
                key={workout.id} 
                className={`bg-white rounded shadow p-4 flex items-center ${
                  workout.completed ? 'border-l-4 border-green-500' : ''
                }`}
              >
                <div className="flex-grow">
                  <h3 className="font-bold">{workout.name}</h3>
                  <p className="text-sm text-gray-600">{workout.description}</p>
                  <div className="flex items-center mt-2">
                    {editingMinutes === workout.id ? (
                      <div className="flex items-center space-x-2">
                        <input
                          type="number"
                          value={minutes}
                          onChange={(e) => setMinutes(e.target.value)}
                          className="w-16 p-1 border rounded"
                          min="1"
                        />
                        <button
                          onClick={() => updateMinutes(workout.id)}
                          className="bg-blue-500 text-white px-2 py-1 rounded text-sm"
                        >
                          Save
                        </button>
                      </div>
                    ) : (
                      <button
                        onClick={() => {
                          setMinutes(String(workout.minutes));
                          setEditingMinutes(workout.id);
                        }}
                        className="text-blue-500 hover:underline text-sm"
                      >
                        {workout.minutes} minutes ({workout.caloriesPerMinute * workout.minutes} calories)
                      </button>
                    )}
                  </div>
                </div>
                <div className="flex space-x-2">
                  <button
                    onClick={() => toggleCompleted(workout.id)}
                    className={`p-2 rounded ${
                      workout.completed 
                        ? 'bg-green-100 text-green-800' 
                        : 'bg-gray-100 text-gray-800'
                    }`}
                  >
                    {workout.completed ? '✓' : '○'}
                  </button>
                  <button
                    onClick={() => removeWorkout(workout.id)}
                    className="p-2 rounded bg-red-100 text-red-800"
                  >
                    ✕
                  </button>
                </div>
              </div>
            ))}
          </div>
          
          <div className="mt-8 flex justify-end">
            <button
              onClick={saveCaloriesToDailyData}
              disabled={calculateCompletedCalories() === 0}
              className={`px-4 py-2 rounded ${
                calculateCompletedCalories() > 0
                  ? 'bg-green-500 text-white hover:bg-green-600'
                  : 'bg-gray-300 text-gray-500 cursor-not-allowed'
              }`}
            >
              Save Completed Calories to Daily Totals
            </button>
          </div>
        </>
      )}
    </div>
  );
}; 