import { UserProfile } from '../types';

// Calculate Basal Metabolic Rate (BMR) using the Mifflin-St Jeor Equation
export const calculateBMR = (profile: UserProfile): number => {
  const { gender, currentWeight, height, age } = profile;
  
  // Convert pounds to kg and inches to cm
  const weightInKg = currentWeight * 0.453592;
  const heightInCm = height * 2.54;
  
  if (gender === 'male') {
    return 10 * weightInKg + 6.25 * heightInCm - 5 * age + 5;
  } else {
    // Female or other
    return 10 * weightInKg + 6.25 * heightInCm - 5 * age - 161;
  }
};

// Calculate Total Daily Energy Expenditure (TDEE)
export const calculateTDEE = (profile: UserProfile): number => {
  const bmr = calculateBMR(profile);
  const { activityLevel } = profile;
  
  const activityMultipliers = {
    sedentary: 1.2, // Little or no exercise
    light: 1.375, // Light exercise 1-3 days/week
    moderate: 1.55, // Moderate exercise 3-5 days/week
    active: 1.725, // Hard exercise 6-7 days/week
    very_active: 1.9 // Very hard exercise & physical job or 2x training
  };
  
  return Math.round(bmr * activityMultipliers[activityLevel]);
};

// Calculate recommended daily calorie intake for weight loss
export const calculateCalorieTarget = (profile: UserProfile, weightLossPerWeek: number = 1): number => {
  const tdee = calculateTDEE(profile);
  
  // 1 lb of fat = 3500 calories, so for losing 1 lb per week, daily deficit = 3500/7 = 500 calories
  const dailyDeficit = weightLossPerWeek * 3500 / 7;
  
  // Minimum recommended calories to prevent nutritional deficiencies
  const minimumCalories = profile.gender === 'male' ? 1500 : 1200;
  
  const recommendedCalories = Math.round(tdee - dailyDeficit);
  
  // Don't go below minimum recommended calories
  return Math.max(recommendedCalories, minimumCalories);
};

// Calculate BMI (Body Mass Index)
export const calculateBMI = (profile: UserProfile): number => {
  const { currentWeight, height } = profile;
  
  // BMI formula: weight(kg) / height(m)²
  const weightInKg = currentWeight * 0.453592;
  const heightInM = height * 0.0254;
  
  return Number((weightInKg / (heightInM * heightInM)).toFixed(1));
};

// Get BMI category
export const getBMICategory = (bmi: number): string => {
  if (bmi < 18.5) return 'Underweight';
  if (bmi < 25) return 'Normal weight';
  if (bmi < 30) return 'Overweight';
  if (bmi < 35) return 'Obesity (Class 1)';
  if (bmi < 40) return 'Obesity (Class 2)';
  return 'Severe Obesity (Class 3)';
}; 