import React, { useState } from 'react';
import { ExerciseTracker } from './components/ExerciseTracker';
import { WeeklyGoalComponent } from './components/WeeklyGoal';
import { Summary } from './components/Summary';
import { Profile } from './components/Profile';
import { WorkoutLibrary } from './components/WorkoutLibrary';
import { DailyPlanView } from './components/DailyPlanView';

type Page = 'home' | 'profile' | 'library' | 'plan' | 'goals' | 'summary';

function App() {
  const [currentPage, setCurrentPage] = useState<Page>('home');

  const renderPage = () => {
    switch (currentPage) {
      case 'profile':
        return <Profile />;
      case 'library':
        return <WorkoutLibrary />;
      case 'plan':
        return <DailyPlanView />;
      case 'goals':
        return <WeeklyGoalComponent />;
      case 'summary':
        return <Summary />;
      default:
        return <ExerciseTracker />;
    }
  };

  return (
    <div className="min-h-screen bg-[var(--primary-dark)]">
      <header className="bg-[var(--secondary-dark)] shadow-md">
        <div className="container mx-auto px-4 py-3 flex flex-col sm:flex-row justify-between items-center">
          <h1 className="text-2xl font-bold text-center sm:text-left mb-4 sm:mb-0 flex items-center">
            <span className="text-[var(--accent)]">Fitness</span>
            <span className="ml-1">Tracker</span>
          </h1>
          <nav className="topnav flex flex-wrap justify-center gap-1">
            <button 
              onClick={() => setCurrentPage('home')}
              className={currentPage === 'home' ? 'active' : ''}
            >
              Daily
            </button>
            <button 
              onClick={() => setCurrentPage('profile')}
              className={currentPage === 'profile' ? 'active' : ''}
            >
              Profile
            </button>
            <button 
              onClick={() => setCurrentPage('library')}
              className={currentPage === 'library' ? 'active' : ''}
            >
              Library
            </button>
            <button 
              onClick={() => setCurrentPage('plan')}
              className={currentPage === 'plan' ? 'active' : ''}
            >
              Plan
            </button>
            <button 
              onClick={() => setCurrentPage('goals')}
              className={currentPage === 'goals' ? 'active' : ''}
            >
              Goals
            </button>
            <button 
              onClick={() => setCurrentPage('summary')}
              className={currentPage === 'summary' ? 'active' : ''}
            >
              Summary
            </button>
          </nav>
        </div>
      </header>
      
      <main className="container mx-auto px-4 py-6">
        {renderPage()}
      </main>
    </div>
  );
}

export default App; 