import { ExerciseData } from '../types';

export const calculateCaloriesBurned = (data: ExerciseData): number => {
  const pushupCalories = (data.pushups / 50) * 20;
  const crunchCalories = (data.crunches / 50) * 15;
  const jumpingJackCalories = (data.jumpingJacks / 50) * 10;
  const walkingCalories = data.milesWalked * 100;
  const runningCalories = data.milesRan * 150;

  return Math.round(pushupCalories + crunchCalories + jumpingJackCalories + walkingCalories + runningCalories);
};

export const calculateDailyCalorieDeficit = (weeklyGoal: number): number => {
  // 1 lb of fat = 3500 calories
  const totalCalorieDeficit = weeklyGoal * 3500;
  return Math.round(totalCalorieDeficit / 7);
};

export const calculateNetCalories = (data: ExerciseData): number => {
  const caloriesBurned = calculateCaloriesBurned(data);
  return data.caloriesEaten - caloriesBurned;
}; 