import React, { useState, useEffect } from 'react';
import { format } from 'date-fns';
import { ExerciseData, DailyData } from '../types';
import { calculateCaloriesBurned, calculateNetCalories } from '../utils/calculations';

const initialExerciseData: ExerciseData = {
  pushups: 0,
  crunches: 0,
  jumpingJacks: 0,
  milesWalked: 0,
  milesRan: 0,
  caloriesEaten: 0,
  proteinGrams: 0,
};

export const ExerciseTracker: React.FC = () => {
  const [exerciseData, setExerciseData] = useState<ExerciseData>(initialExerciseData);
  const [history, setHistory] = useState<DailyData[]>([]);

  useEffect(() => {
    const savedData = localStorage.getItem('exerciseHistory');
    if (savedData) {
      setHistory(JSON.parse(savedData));
    }
  }, []);

  const saveToHistory = () => {
    const today = format(new Date(), 'yyyy-MM-dd');
    const newEntry: DailyData = {
      ...exerciseData,
      date: today,
    };

    const updatedHistory = [...history, newEntry];
    setHistory(updatedHistory);
    localStorage.setItem('exerciseHistory', JSON.stringify(updatedHistory));
    setExerciseData(initialExerciseData);
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setExerciseData(prev => ({
      ...prev,
      [name]: Number(value),
    }));
  };

  const resetDay = () => {
    setExerciseData(initialExerciseData);
  };

  const undoLastEntry = () => {
    const newHistory = history.slice(0, -1);
    setHistory(newHistory);
    localStorage.setItem('exerciseHistory', JSON.stringify(newHistory));
  };

  const CalorieCircle = ({ value, total, label }: { value: number, total: number, label: string }) => {
    const percentage = Math.min(100, (value / (total || 1)) * 100);
    const radius = 40;
    const circumference = 2 * Math.PI * radius;
    const strokeDashoffset = circumference - (percentage / 100) * circumference;
    
    return (
      <div className="circular-progress">
        <svg width="100" height="100" viewBox="0 0 100 100">
          <circle
            className="circular-progress-bg"
            cx="50"
            cy="50"
            r={radius}
            fill="transparent"
            strokeWidth="8"
          />
          <circle
            className="circular-progress-fill"
            cx="50"
            cy="50"
            r={radius}
            fill="transparent"
            strokeWidth="8"
            strokeDasharray={circumference}
            strokeDashoffset={strokeDashoffset}
          />
        </svg>
        <div className="absolute flex flex-col items-center">
          <span className="text-xl font-bold accent-text">{value}</span>
          <span className="text-xs text-[var(--text-secondary)]">{label}</span>
        </div>
      </div>
    );
  };

  return (
    <div className="max-w-2xl mx-auto">
      <h2 className="text-2xl font-bold mb-6 flex items-center">
        <span className="text-[var(--text-primary)]">DAILY DATA</span>
        <span className="ml-2 text-[var(--accent)]">{format(new Date(), 'dd')}</span>
      </h2>
      
      <div className="dark-card p-4 mb-6">
        <div className="space-y-4">
          {Object.entries(exerciseData).map(([key, value]) => {
            // Skip caloriesEaten and proteinGrams
            if (key === 'caloriesEaten' || key === 'proteinGrams') return null;

            // Format the label
            const label = key.replace(/([A-Z])/g, ' $1').replace(/^./, str => str.toUpperCase());
            
            return (
              <div key={key} className="flex justify-between items-center border-b border-[#333333] pb-2">
                <div className="flex items-center">
                  <span className="inline-block w-3 h-3 rounded-full bg-[var(--accent)] mr-2"></span>
                  <label className="text-[var(--text-primary)]">{label}</label>
                </div>
                <input
                  type="number"
                  name={key}
                  value={value}
                  onChange={handleInputChange}
                  className="w-20 bg-transparent text-right text-[var(--accent)] focus:outline-none"
                />
              </div>
            );
          })}
        </div>

        <div className="mt-6 grid grid-cols-2 gap-4">
          <div className="dark-card p-4 flex flex-col items-center">
            <CalorieCircle 
              value={calculateCaloriesBurned(exerciseData)} 
              total={2000} 
              label="Burned" 
            />
          </div>
          <div className="dark-card p-4 flex flex-col items-center">
            <CalorieCircle 
              value={Math.abs(calculateNetCalories(exerciseData))} 
              total={2000} 
              label="Deficit" 
            />
          </div>
        </div>
      </div>

      <div className="dark-card p-4 mb-6">
        <h3 className="text-lg font-semibold mb-4">Nutrition</h3>
        <div className="space-y-4">
          <div className="flex justify-between items-center border-b border-[#333333] pb-2">
            <div className="flex items-center">
              <span className="inline-block w-3 h-3 rounded-full bg-[var(--warning)] mr-2"></span>
              <label className="text-[var(--text-primary)]">Calories Eaten</label>
            </div>
            <input
              type="number"
              name="caloriesEaten"
              value={exerciseData.caloriesEaten}
              onChange={handleInputChange}
              className="w-20 bg-transparent text-right text-[var(--warning)] focus:outline-none"
            />
          </div>
          <div className="flex justify-between items-center border-b border-[#333333] pb-2">
            <div className="flex items-center">
              <span className="inline-block w-3 h-3 rounded-full bg-[var(--success)] mr-2"></span>
              <label className="text-[var(--text-primary)]">Protein (g)</label>
            </div>
            <input
              type="number"
              name="proteinGrams"
              value={exerciseData.proteinGrams}
              onChange={handleInputChange}
              className="w-20 bg-transparent text-right text-[var(--success)] focus:outline-none"
            />
          </div>
        </div>
      </div>

      <div className="flex space-x-3">
        <button
          onClick={saveToHistory}
          className="primary-button flex-1"
        >
          Save Day
        </button>
        <button
          onClick={resetDay}
          className="secondary-button flex-1"
        >
          Reset
        </button>
        <button
          onClick={undoLastEntry}
          className="secondary-button flex-1"
          disabled={history.length === 0}
        >
          Undo
        </button>
      </div>
    </div>
  );
}; 