import React, { useEffect, useState } from 'react';
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
  BarChart,
  Bar,
} from 'recharts';
import { DailyData, WeeklyGoal } from '../types';
import { calculateCaloriesBurned, calculateNetCalories } from '../utils/calculations';

export const Summary: React.FC = () => {
  const [history, setHistory] = useState<DailyData[]>([]);
  const [weeklyGoal, setWeeklyGoal] = useState<WeeklyGoal | null>(null);

  useEffect(() => {
    const savedHistory = localStorage.getItem('exerciseHistory');
    const savedGoal = localStorage.getItem('weeklyGoal');
    
    if (savedHistory) {
      setHistory(JSON.parse(savedHistory));
    }
    if (savedGoal) {
      setWeeklyGoal(JSON.parse(savedGoal));
    }
  }, []);

  // Calculate total calories burned, including both tracked exercises and planned workouts
  const getTotalCaloriesBurned = (entry: DailyData): number => {
    const trackedExerciseCalories = calculateCaloriesBurned(entry);
    const plannedWorkoutCalories = entry.plannedWorkoutCalories || 0;
    return trackedExerciseCalories + plannedWorkoutCalories;
  };

  // Calculate net calories considering both tracked exercises and planned workouts
  const getTotalNetCalories = (entry: DailyData): number => {
    const totalBurned = getTotalCaloriesBurned(entry);
    return entry.caloriesEaten - totalBurned;
  };

  const chartData = history.map(entry => ({
    date: entry.date.slice(-2), // Just show the day
    caloriesBurned: getTotalCaloriesBurned(entry),
    netCalories: getTotalNetCalories(entry),
    protein: entry.proteinGrams,
  }));

  const totalCaloriesBurned = history.reduce(
    (sum, entry) => sum + getTotalCaloriesBurned(entry),
    0
  );

  const averageNetCalories = history.length
    ? history.reduce((sum, entry) => sum + getTotalNetCalories(entry), 0) / history.length
    : 0;

  const totalProtein = history.reduce((sum, entry) => sum + entry.proteinGrams, 0);

  const renderMetricCard = (title: string, value: number, unit: string, color: string) => (
    <div className="metric-card">
      <span className="metric-title">{title}</span>
      <div className="flex items-baseline">
        <span className="metric-value" style={{ color }}>{value.toLocaleString()}</span>
        <span className="ml-1 text-sm text-[var(--text-secondary)]">{unit}</span>
      </div>
    </div>
  );

  return (
    <div className="max-w-4xl mx-auto">
      <h2 className="text-2xl font-bold mb-6">Progress Summary</h2>

      <div className="dark-card p-4 mb-6">
        <h3 className="text-lg font-semibold mb-4">Calories Burned</h3>
        <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-6">
          {renderMetricCard("Total", totalCaloriesBurned, "cal", "var(--accent)")}
          {renderMetricCard("Daily Avg", Math.round(totalCaloriesBurned / (history.length || 1)), "cal", "var(--accent)")}
          {renderMetricCard("Net Avg", Math.round(averageNetCalories), "cal", "var(--warning)")}
          {renderMetricCard("Protein", totalProtein, "g", "var(--success)")}
        </div>

        {weeklyGoal && (
          <div className="mt-4">
            <div className="flex justify-between text-sm mb-1">
              <span className="text-[var(--text-secondary)]">Weekly Goal Progress</span>
              <span className="text-[var(--accent)]">
                {((totalCaloriesBurned / (weeklyGoal.targetWeightLoss * 3500)) * 100).toFixed(1)}%
              </span>
            </div>
            <div className="progress-bar-container">
              <div 
                className="progress-bar"
                style={{ 
                  width: `${Math.min(100, (totalCaloriesBurned / (weeklyGoal.targetWeightLoss * 3500)) * 100)}%` 
                }}
              ></div>
            </div>
          </div>
        )}
      </div>

      <div className="dark-card p-4 mb-6">
        <h3 className="text-lg font-semibold mb-4">Calories Trend</h3>
        <div className="h-64">
          <ResponsiveContainer width="100%" height="100%">
            <BarChart data={chartData}>
              <CartesianGrid strokeDasharray="3 3" stroke="#333333" />
              <XAxis dataKey="date" stroke="#AAAAAA" />
              <YAxis stroke="#AAAAAA" />
              <Tooltip 
                contentStyle={{ backgroundColor: '#222222', border: 'none', borderRadius: '8px' }}
                labelStyle={{ color: '#FFFFFF' }}
              />
              <Legend />
              <Bar dataKey="caloriesBurned" name="Calories Burned" fill="var(--accent)" radius={[4, 4, 0, 0]} />
              <Bar dataKey="netCalories" name="Net Calories" fill="var(--warning)" radius={[4, 4, 0, 0]} />
            </BarChart>
          </ResponsiveContainer>
        </div>
      </div>

      <div className="dark-card p-4">
        <h3 className="text-lg font-semibold mb-4">Protein Intake</h3>
        <div className="h-64">
          <ResponsiveContainer width="100%" height="100%">
            <LineChart data={chartData}>
              <CartesianGrid strokeDasharray="3 3" stroke="#333333" />
              <XAxis dataKey="date" stroke="#AAAAAA" />
              <YAxis stroke="#AAAAAA" />
              <Tooltip 
                contentStyle={{ backgroundColor: '#222222', border: 'none', borderRadius: '8px' }}
                labelStyle={{ color: '#FFFFFF' }}
              />
              <Legend />
              <Line
                type="monotone"
                dataKey="protein"
                stroke="var(--success)"
                name="Protein (g)"
                dot={{ fill: 'var(--success)', r: 4 }}
                activeDot={{ r: 6 }}
              />
            </LineChart>
          </ResponsiveContainer>
        </div>
      </div>
    </div>
  );
}; 