import React, { useState, useEffect } from 'react';
import { format, addDays } from 'date-fns';
import { WeeklyGoal } from '../types';
import { calculateDailyCalorieDeficit } from '../utils/calculations';

export const WeeklyGoalComponent: React.FC = () => {
  const [goal, setGoal] = useState<WeeklyGoal | null>(null);

  useEffect(() => {
    const savedGoal = localStorage.getItem('weeklyGoal');
    if (savedGoal) {
      setGoal(JSON.parse(savedGoal));
    }
  }, []);

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    const formData = new FormData(e.currentTarget);
    const targetWeightLoss = Number(formData.get('targetWeightLoss'));
    const startDate = format(new Date(), 'yyyy-MM-dd');
    const endDate = format(addDays(new Date(), 7), 'yyyy-MM-dd');

    const newGoal: WeeklyGoal = {
      targetWeightLoss,
      startDate,
      endDate,
    };

    setGoal(newGoal);
    localStorage.setItem('weeklyGoal', JSON.stringify(newGoal));
  };

  return (
    <div className="max-w-2xl mx-auto p-4">
      <h2 className="text-2xl font-bold mb-4">Weekly Weight Loss Goal</h2>

      {!goal ? (
        <form onSubmit={handleSubmit} className="space-y-4">
          <div>
            <label className="block mb-2">Target Weight Loss (lbs)</label>
            <input
              type="number"
              name="targetWeightLoss"
              step="0.1"
              required
              className="w-full p-2 border rounded"
            />
          </div>
          <button
            type="submit"
            className="bg-blue-500 text-white px-4 py-2 rounded hover:bg-blue-600"
          >
            Set Goal
          </button>
        </form>
      ) : (
        <div className="space-y-4">
          <p>Target Weight Loss: {goal.targetWeightLoss} lbs</p>
          <p>Start Date: {goal.startDate}</p>
          <p>End Date: {goal.endDate}</p>
          <p>
            Required Daily Calorie Deficit:{' '}
            {calculateDailyCalorieDeficit(goal.targetWeightLoss)} calories
          </p>
          <button
            onClick={() => {
              setGoal(null);
              localStorage.removeItem('weeklyGoal');
            }}
            className="bg-red-500 text-white px-4 py-2 rounded hover:bg-red-600"
          >
            Reset Goal
          </button>
        </div>
      )}
    </div>
  );
}; 