import React, { useState, useEffect } from 'react';
import { format } from 'date-fns';
import { UserProfile, WeightEntry } from '../types';
import { calculateBMI, getBMICategory, calculateBMR, calculateCalorieTarget, calculateTDEE } from '../utils/profileCalculations';
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
  Area,
  AreaChart,
} from 'recharts';

const initialProfile: UserProfile = {
  name: '',
  age: 30,
  gender: 'male',
  height: 70, // 5'10" in inches
  currentWeight: 180,
  goalWeight: 160,
  activityLevel: 'moderate',
};

export const Profile: React.FC = () => {
  const [profile, setProfile] = useState<UserProfile | null>(null);
  const [isEditing, setIsEditing] = useState(false);
  const [weightEntries, setWeightEntries] = useState<WeightEntry[]>([]);
  const [newWeight, setNewWeight] = useState('');

  useEffect(() => {
    // Load profile from localStorage
    const savedProfile = localStorage.getItem('userProfile');
    const savedWeightEntries = localStorage.getItem('weightHistory');
    
    if (savedProfile) {
      setProfile(JSON.parse(savedProfile));
    }
    
    if (savedWeightEntries) {
      setWeightEntries(JSON.parse(savedWeightEntries));
    }
  }, []);

  const handleProfileSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    const formData = new FormData(e.currentTarget);
    
    const newProfile: UserProfile = {
      name: String(formData.get('name') || ''),
      age: Number(formData.get('age') || 30),
      gender: (formData.get('gender') as 'male' | 'female' | 'other') || 'male',
      height: Number(formData.get('height') || 70),
      currentWeight: Number(formData.get('currentWeight') || 180),
      goalWeight: Number(formData.get('goalWeight') || 160),
      activityLevel: (formData.get('activityLevel') as UserProfile['activityLevel']) || 'moderate',
    };
    
    setProfile(newProfile);
    setIsEditing(false);
    
    // Save to localStorage
    localStorage.setItem('userProfile', JSON.stringify(newProfile));
    
    // Add initial weight entry if there are none
    if (weightEntries.length === 0) {
      const initialEntry: WeightEntry = {
        date: format(new Date(), 'yyyy-MM-dd'),
        weight: newProfile.currentWeight,
      };
      
      const updatedEntries = [initialEntry];
      setWeightEntries(updatedEntries);
      localStorage.setItem('weightHistory', JSON.stringify(updatedEntries));
    }
  };

  const addWeightEntry = () => {
    if (!newWeight || !profile) return;
    
    const weight = parseFloat(newWeight);
    if (isNaN(weight)) return;
    
    const newEntry: WeightEntry = {
      date: format(new Date(), 'yyyy-MM-dd'),
      weight,
    };
    
    const updatedEntries = [...weightEntries, newEntry];
    
    // Update profile current weight
    const updatedProfile = {
      ...profile,
      currentWeight: weight,
    };
    
    setWeightEntries(updatedEntries);
    setProfile(updatedProfile);
    setNewWeight('');
    
    // Save to localStorage
    localStorage.setItem('weightHistory', JSON.stringify(updatedEntries));
    localStorage.setItem('userProfile', JSON.stringify(updatedProfile));
  };

  const renderProfileForm = () => (
    <form onSubmit={handleProfileSubmit} className="space-y-4">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <div>
          <label className="block mb-2 text-[var(--text-secondary)]">Name</label>
          <input
            type="text"
            name="name"
            defaultValue={profile?.name}
            className="dark-input w-full"
          />
        </div>
        <div>
          <label className="block mb-2 text-[var(--text-secondary)]">Age</label>
          <input
            type="number"
            name="age"
            defaultValue={profile?.age || initialProfile.age}
            className="dark-input w-full"
          />
        </div>
        <div>
          <label className="block mb-2 text-[var(--text-secondary)]">Gender</label>
          <select
            name="gender"
            defaultValue={profile?.gender || initialProfile.gender}
            className="dark-input w-full"
          >
            <option value="male">Male</option>
            <option value="female">Female</option>
            <option value="other">Other</option>
          </select>
        </div>
        <div>
          <label className="block mb-2 text-[var(--text-secondary)]">Height (inches)</label>
          <input
            type="number"
            name="height"
            defaultValue={profile?.height || initialProfile.height}
            className="dark-input w-full"
          />
        </div>
        <div>
          <label className="block mb-2 text-[var(--text-secondary)]">Current Weight (lbs)</label>
          <input
            type="number"
            name="currentWeight"
            defaultValue={profile?.currentWeight || initialProfile.currentWeight}
            className="dark-input w-full"
          />
        </div>
        <div>
          <label className="block mb-2 text-[var(--text-secondary)]">Goal Weight (lbs)</label>
          <input
            type="number"
            name="goalWeight"
            defaultValue={profile?.goalWeight || initialProfile.goalWeight}
            className="dark-input w-full"
          />
        </div>
        <div className="md:col-span-2">
          <label className="block mb-2 text-[var(--text-secondary)]">Activity Level</label>
          <select
            name="activityLevel"
            defaultValue={profile?.activityLevel || initialProfile.activityLevel}
            className="dark-input w-full"
          >
            <option value="sedentary">Sedentary (little to no exercise)</option>
            <option value="light">Light (1-3 days/week)</option>
            <option value="moderate">Moderate (3-5 days/week)</option>
            <option value="active">Active (6-7 days/week)</option>
            <option value="very_active">Very Active (2x per day)</option>
          </select>
        </div>
      </div>
      <div className="flex justify-end space-x-4">
        <button
          type="button"
          onClick={() => setIsEditing(false)}
          className="secondary-button"
        >
          Cancel
        </button>
        <button
          type="submit"
          className="primary-button"
        >
          Save
        </button>
      </div>
    </form>
  );

  const renderMetrics = () => {
    if (!profile) return null;
    
    const bmi = calculateBMI(profile);
    const bmr = Math.round(calculateBMR(profile));
    const tdee = calculateTDEE(profile);
    const calorieTarget = calculateCalorieTarget(profile);
    const weightToLose = profile.currentWeight - profile.goalWeight;
    const weightLossProgress = weightEntries.length > 1 
      ? profile.currentWeight - weightEntries[0].weight
      : 0;
    
    return (
      <div className="space-y-6">
        <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
          <div className="metric-card">
            <span className="metric-title">BMI</span>
            <div className="flex flex-col">
              <span className="metric-value">{bmi}</span>
              <span className="text-xs text-[var(--text-secondary)]">{getBMICategory(bmi)}</span>
            </div>
          </div>
          <div className="metric-card">
            <span className="metric-title">BMR</span>
            <div className="flex items-baseline">
              <span className="metric-value">{bmr}</span>
              <span className="ml-1 text-xs text-[var(--text-secondary)]">calories</span>
            </div>
          </div>
          <div className="metric-card">
            <span className="metric-title">TDEE</span>
            <div className="flex items-baseline">
              <span className="metric-value">{tdee}</span>
              <span className="ml-1 text-xs text-[var(--text-secondary)]">calories</span>
            </div>
          </div>
          <div className="metric-card">
            <span className="metric-title">Calorie Target</span>
            <div className="flex items-baseline">
              <span className="metric-value text-[var(--accent)]">{calorieTarget}</span>
              <span className="ml-1 text-xs text-[var(--text-secondary)]">calories</span>
            </div>
          </div>
        </div>
        
        <div className="dark-card p-5">
          <h3 className="text-lg font-semibold mb-4">Weight Loss Progress</h3>
          <div className="mb-4">
            <div className="flex justify-between text-sm mb-1">
              <span className="text-[var(--text-secondary)]">Target Weight Loss: {weightToLose.toFixed(1)} lbs</span>
              <span className="text-[var(--accent)]">
                {((weightLossProgress / weightToLose) * 100).toFixed(1)}%
              </span>
            </div>
            <div className="progress-bar-container">
              <div 
                className="progress-bar"
                style={{ 
                  width: `${Math.min(100, Math.max(0, (weightLossProgress / weightToLose) * 100))}%` 
                }}
              ></div>
            </div>
          </div>
          <div className="flex justify-between">
            <div>
              <div className="text-[var(--text-secondary)] text-sm">Current</div>
              <div className="text-xl font-semibold">{profile.currentWeight} lbs</div>
            </div>
            <div className="text-right">
              <div className="text-[var(--text-secondary)] text-sm">Goal</div>
              <div className="text-xl font-semibold">{profile.goalWeight} lbs</div>
            </div>
          </div>
          <div className="mt-2">
            {weightLossProgress > 0 
              ? <div className="text-[var(--success)]">You've lost {weightLossProgress.toFixed(1)} lbs so far!</div>
              : <div className="text-[var(--text-secondary)]">Start tracking your weight loss journey!</div>}
          </div>
        </div>
      </div>
    );
  };

  return (
    <div className="max-w-4xl mx-auto">
      <h2 className="text-2xl font-bold mb-6">Your Profile</h2>
      
      {profile && !isEditing ? (
        <div className="space-y-6">
          <div className="dark-card p-5 flex justify-between items-start">
            <div>
              <h3 className="text-xl font-semibold">{profile.name || 'Fitness Enthusiast'}</h3>
              <p className="text-[var(--text-secondary)]">
                {profile.gender === 'male' ? 'Male' : profile.gender === 'female' ? 'Female' : 'Other'}, {profile.age} years old
              </p>
              <p className="text-[var(--text-secondary)]">
                {Math.floor(profile.height / 12)}'{profile.height % 12}", {profile.currentWeight} lbs
              </p>
            </div>
            <button
              onClick={() => setIsEditing(true)}
              className="primary-button"
            >
              Edit Profile
            </button>
          </div>
          
          {renderMetrics()}
          
          <div className="dark-card p-5">
            <h3 className="text-lg font-semibold mb-4">Weight Tracking</h3>
            
            <div className="flex space-x-3 mb-6">
              <input
                type="number"
                placeholder="Enter today's weight"
                value={newWeight}
                onChange={(e) => setNewWeight(e.target.value)}
                className="dark-input flex-grow"
              />
              <button
                onClick={addWeightEntry}
                className="primary-button"
              >
                Add Entry
              </button>
            </div>
            
            {weightEntries.length > 0 && (
              <div className="h-64">
                <ResponsiveContainer width="100%" height="100%">
                  <AreaChart data={weightEntries.map(entry => ({
                    date: entry.date.slice(-5), // Show MM-DD
                    weight: entry.weight
                  }))}>
                    <CartesianGrid strokeDasharray="3 3" stroke="#333333" />
                    <XAxis dataKey="date" stroke="#AAAAAA" />
                    <YAxis domain={['dataMin - 5', 'dataMax + 5']} stroke="#AAAAAA" />
                    <Tooltip 
                      contentStyle={{ backgroundColor: '#222222', border: 'none', borderRadius: '8px' }}
                      labelStyle={{ color: '#FFFFFF' }}
                    />
                    <Area
                      type="monotone"
                      dataKey="weight"
                      stroke="var(--accent)"
                      fill="rgba(0, 230, 255, 0.1)"
                      name="Weight (lbs)"
                    />
                  </AreaChart>
                </ResponsiveContainer>
              </div>
            )}
          </div>
        </div>
      ) : isEditing ? (
        <div className="dark-card p-5">
          {renderProfileForm()}
        </div>
      ) : (
        <div className="dark-card p-5 text-center">
          <h3 className="text-xl font-semibold mb-4">Welcome to Fitness Tracker!</h3>
          <p className="mb-6 text-[var(--text-secondary)]">Let's set up your profile to get personalized recommendations.</p>
          {renderProfileForm()}
        </div>
      )}
    </div>
  );
}; 